.documentation_page .box-sub2-title {
  background: rgba(61, 123, 194, 0.078);
  font-size: calc(24px + 2 * ( (100vw - 576px) / 1024));
  margin: 2rem 0;
}

@media screen and (max-width: 576px) {
  .documentation_page .box-sub2-title {
    font-size: 24px;
  }
}

@media screen and (min-width: 1600px) {
  .documentation_page .box-sub2-title {
    font-size: 26px;
  }
}

.documentation_page .box-sub2-title::before {
  content: "##";
  color: #be346a;
}

.documentation_page .structure {
  background: #ffffff;
  border-radius: 5px;
  padding: 30px 60px !important;
  border: solid 1px #f7f7f7 !important;
  box-shadow: 0 33px 88px 0 #1d0e6211;
  transition: 0.3s;
}

.documentation_page .structure:hover {
  box-shadow: none;
}

.documentation_page .structure .fa-folder-open {
  color: #ffe16e;
}

.documentation_page .structure .fa-folder {
  color: #afa8ff;
}

.documentation_page .structure .file {
  color: #ffa8a8;
}

.documentation_page .structure .file2 {
  color: #a8ffe5;
}

.documentation_page .structure .file3 {
  color: #aea8ff;
}

.documentation_page #docsSidenav {
  padding: 60px;
  background-color: #faf4f6;
  border-right: 1px solid #f3f8ff;
  max-width: 21rem;
}

.documentation_page #docsSidenav h5 {
  text-transform: uppercase;
  font-size: 15px;
  letter-spacing: 1px;
  margin-bottom: 20px !important;
  margin-top: 20px !important;
  display: flex;
  align-items: center;
}

.documentation_page #docsSidenav a {
  font-size: 15px;
}

.documentation_page #docsSidenav .icon {
  margin-right: 10px;
}

.documentation_page #docsSidenav .icon svg {
  fill: #10154e !important;
}

.documentation_page .border {
  margin-bottom: 2rem;
  margin-top: 2rem;
  border-top: 1px solid #eaf2fe;
}

pre[class*="language-"] {
  font-size: 14px;
  padding: 1em;
  margin: 0;
  overflow: auto;
  border: none;
  border-radius: 0.5em;
  box-shadow: none;
  border-radius: 5px;
}

pre[class*="language-"],
:not(pre) > code[class*="language-"] {
  background-color: #1f2937 !important;
}

div.code-toolbar > .toolbar a,
div.code-toolbar > .toolbar .copy,
div.code-toolbar > .toolbar span {
  color: white;
  font-size: 0.8em;
  padding: 10px 1rem;
  background: #be346a !important;
  box-shadow: 0 2px 0 0 rgba(0, 0, 0, 0.2);
  border-radius: 0.5em;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: #706699;
}

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #6edfff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #baea80;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
  color: #b9a5ca;
}

span.inline-color-wrapper {
  height: 16px;
  width: 16px;
  margin: 0 6px 0 0;
  box-sizing: border-box;
  border: 1px solid #61448a;
  outline: none;
  border-radius: 50%;
}
